<template>
   <div class="scopePage threePadding">
     <div class="toolbox">
          <span>时间选择 : </span>&nbsp;
          <div class="layoutBox" style="width:242px">
            <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" value-format="yyyy-MM-dd"></el-date-picker>
          </div>&emsp;&emsp;
          <span>监测项目 : </span>&nbsp;
          <div class="layoutBox" style="width:120px">
            <el-select v-model="factorModel" multiple collapse-tags placeholder="请选择" @change="selectChange">
                  <el-option
                    v-for="item in factorData"
                    :key="item.projectCode"
                    :label="item.projectName"
                    :value="item.projectCode">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp;
          <el-button @click="search"><i class="iconfont icon-sousuo"></i>查询</el-button>
          <el-button type='delet' @click="exportExcel"><i class="iconfont icon-daochu"></i>导出</el-button>
     </div>
    <div class="table report-table-wrap" style="height:calc(100% - 44px)">
          <el-table border :data="tableData" height="calc(100% - 30px)"  ref="table"
          v-loading="loading"  element-loading-text="加载中"
               element-loading-spinner="loading-type-a"
               element-loading-background="rgba(255, 255, 255, 1)">
                <el-table-column type='index' label='序号' width='80'></el-table-column>
                <el-table-column  label='时间' prop='time' width='200'></el-table-column>
                <el-table-column :label='getfactorName(item)' width='120' v-for="(item,key) in tableHead" :key="key">
                      <template slot-scope="scope">
                        <div class="scopeBox" :class="scope.row[item+'level']=='I类' || scope.row[item+'level']=='II类'?'classOt':
                        scope.row[item+'level']=='III类'?'classThree':
                        scope.row[item+'level']=='IV类'?'classFour':
                        scope.row[item+'level']=='V类'?'classFive':
                        scope.row[item+'level']=='劣V类'?'classS':''">
                            {{scope.row[item+'level']}}
                        </div>
                      </template>
 
                </el-table-column>
                <el-table-column  label='综合水质类别' prop='level' >
                     <template slot-scope="scope">
                        <div class="scopeBox" :class="scope.row['level']=='I类' || scope.row['level']=='II类'?'classOt':
                        scope.row['level']=='III类'?'classThree':
                        scope.row['level']=='IV类'?'classFour':
                        scope.row['level']=='V类'?'classFive':
                        scope.row['level']=='劣V类'?'classS':''">
                            {{scope.row['level']}}
                        </div>
                      </template>
                </el-table-column>
          </el-table>
          <div class="paginationBox">
              <el-pagination
                @size-change="paginationSizeChange"
                @current-change="paginationChange"
                :current-page.sync="currentPage"
                :page-size="pageSize"
                background
                :small = "true"
                popper-class = "select-common-option"
                :page-sizes = "pageList"
                layout="total,sizes,slot,->,prev, pager, next, jumper"
                :total="total">
                <span class="el-pagination__current">当前第{{currentPage}}/{{totalPageNumber}}页</span>
              </el-pagination>
          </div>
    </div>
  </div>
</template>

<script>
import {exportTableData} from '../../assets/js/tableExport'
export default {
  name: "",
  data() {
    return {
      dateRange: [],
      value: "",
      factorData: [], //监测项目
      factorModel: [],
      tableData: [],
      tableHead: [],
      loading:true,
      currentPage: 1, //分页组件当前页
      pageSize: 10, //分页每页多少条数据
      total: 0, //总条数
      pageList: [10, 20, 30], //设置每页多少条
      fieldList:[],
    };
  },
  created() {
    this.init()
  
  },
  methods: {
    initTime(){
        //初始化时间
      let startTime = new Date().setDate(new Date().getDate() - 6);
      let endTime = new Date();
      this.dateRange = [
        new Date(startTime).format("yyyy-MM-dd"),
        endTime.format("yyyy-MM-dd")
      ];
    },
    selectChange(arr) {
      this.factorModel = arr;
    },
    //查询
    search() {
        this.getTableData()
    },
    getfactorName(item){
         let fName = "";
      for (let i in this.factorData) {
        if (item == this.factorData[i].projectCode) {
          fName = this.factorData[i].projectName;
        }
      }
      return fName;
    },
    //获取页面搜索各下拉框数据
    init() {
      this.initTime()
      //获取参数因子数据
      this.$http.post("/SiteManagement/index/query").then(res => {
        if (res.data.code == 0) {
          this.factorData = res.data.content.dataList;
          for(let i=0;i<this.factorData.length;i++){
            this.factorModel.push(this.factorData[i].projectCode)
          }
        }
      });
      
    },
    paginationSizeChange(v){
      this.pageSize=v;
      this.getTableData()
    },
    paginationChange(v){
      this.currentPage=v;
       this.getTableData()
    },
     //获取页面表格数据
    getTableData() {
     
      this.loading=true;
      
      this.$http
        .get("/StatisticsAndAlarm/dataReports/queryDatasStageReports", {
          params: {
            startTime: this.dateRange[0],
            endTime: this.dateRange[1],
            stationCode: this.stationId,
            type:"day",
            factorCode: this.factorModel.join(","),
            pageNumber:this.currentPage,
            pageSize:this.pageSize,
          }
        })
        .then(res => {
           if(res.data.code == 0){
              this.tableHead=JSON.parse(JSON.stringify(this.factorModel));
              this.tableData = res.data.content.dataList;
              this.total=res.data.content.totalRecords;
              this.loading=false;
           }
           
        });
    },
     //导出报表
    exportExcel(){
        let {table} = this.$refs;
        let tableBox = table.$el;
        let header = tableBox.querySelector('.el-table__header');
        let title = '断面时段水质统计';
        this.fieldList=["time"]
        for(let i =0;i<this.tableHead.length;i++){
         
          this.fieldList.push(this.tableHead[i]+'level')
         
        };
       
        this.fieldList.push("level")
        let params = {
          headerProp:this.fieldList.join(','),
          startTime: this.dateRange[0],
          endTime: this.dateRange[1],
          stationCode: this.stationId,
          type:"day",
          factorCode: this.factorModel.join(","),
          pattern:'yyyy-MM-dd',
        };
        exportTableData(header,title,'','1',"/StatisticsAndAlarm/exports/datasStageExcel",params);
    }
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    },
    totalPageNumber() {
      //计算总页数
      let number = 0;
      number = Math.ceil(this.total / this.pageSize);
      return number;
    }
  },
  watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;
         
         this.getTableData();
      }
    },
   
  }
};
</script>

<style scope lang="less">
.scopePage {
  height: 100%;
  .table{
    .scopeBox{
      color: #fff;
    }
    .classOt{
       background: #00A1E4;
      
    }
    .classThree{
       background: #24C768;
    }
    .classFour{
     background: #E5CE10;
    }
    .classFive{
     background: #FF7E00;
    }
    .classS{
      background: #ff0000;
    }
  }
}
</style>



